<template lang="pug">
  .spinner(:class="color" :style="style")
    icon.icon(name="spinner")
</template>

<script>
export default {
  name: 'spinner',

  props: {
    color: {type: String, default: 'grey'}, // grey, white
    width: {type: [String, Number], default: 0},
    height: {type: [String, Number], default: 0},
    size: {type: String, default: 'middle'}, // small, big
  },

  computed: {
    style() {
      let width, height
      if (this.width) width = this.width + 'px'
      if (this.height) height = this.height + 'px'
      if (!this.width && !this.height) {
        if (this.size === 'big') {
          width = '39px'
          height = '39px'
        } else if (this.size === 'small') {
          width = '13px'
          height = '13px'
        } else {
          width = '26px'
          height = '26px'
        }
      }
      return {width, height}
    },
  },
}
</script>

<style scoped lang="sass">
.spinner
  margin: 0 auto
  .icon
    fill: $grey4
    animation: rotating 1s linear infinite
    transform: scale(2)

  &.white
    .icon
      fill: $white1



@keyframes rotating
  from
    transform: rotate(0deg)
  to
    transform: rotate(360deg)
</style>
